/* 2. Mandatory patching of @ui5/webcomponents controls to work well with VSCode theme mapping. */
/* trying to get rid of the mini scrollbars on tokens due to the shifted font baseline */
ui5-multi-combobox {
    --sapFontSize: 0.74em;
}

/* fix switch handle transparency problems */
html:root {
    --_ui5_switch_semantic_button_background: var(--_ui5_switch_handle_bg);
    --_ui5_switch_handle_semantic_checked_hover_bg: var(--sapPositiveColor);
    --_ui5-switch_handle-off-hover_box_shadow: inset 0 0 0 20px var(--vscode-inputValidation-infoBackground);
    --_ui5_switch_handle_disabled_semantic_checked_bg: var(--_ui5_switch_handle_bg);
}